import React from 'react';
import { Link } from 'react-router-dom';
import { MdPlayArrow, MdPause, MdArrowForwardIos, MdArrowBackIosNew, } from 'react-icons/md';
import LocaleContext from '../contexts/LocaleContext';
import { music } from '../utils/playlist';

function MusicPlayer() {
  const audioPlayer = React.useRef();
  const { idLyricContextValue } = React.useContext(LocaleContext);
  const { toggleIdLyric } = idLyricContextValue;


  const [ index, setIndex ] = React.useState(0);
  const [ currentTitle, setCurrentTitle ] = React.useState(music[0].title);
  const [ currentSinger, setCurrentSinger ] = React.useState(music[0].singer);
  const [ currentImg, setCurrentImg ] = React.useState(music[0].img);
  const [ currentSong, setCurrentSong ] = React.useState(music[index].src);
  const [ isPlaying, setIsPlaying ] = React.useState(false);

  const [ elapsed, setElapsed ] = React.useState(0);
  const [ duration, setDuration ] = React.useState(0);

  React.useEffect(() => {
    if (isPlaying) {
      const interval = setInterval(() => {
        const _elapsed = Math.floor(audioPlayer.current.currentTime);
        const _duration = Math.floor(audioPlayer.current.duration);

        setDuration(_duration);
        setElapsed(_elapsed);
      }, 100);
      return () => clearInterval(interval);
    }
  }, [isPlaying]);

  const togglePlay = () => {
    if (isPlaying) {
      audioPlayer.current.pause();
    } else {
      audioPlayer.current.play();
    }

    setIsPlaying(prev => !prev);
  }

  const togglePrev = () => {
    if (index > 0) {
      setIndex(prev => prev - 1);
      audioPlayer.current.src = music[index - 1].src;
      setCurrentTitle(music[index - 1].title);
      setCurrentSinger(music[index - 1].singer);
      setCurrentImg(music[index - 1].img);
      toggleIdLyric(music[index - 1].id);

      if (isPlaying) {
        audioPlayer.current.play();
      }
    }
  }

  const toggleNext = () => {
    if (index >= music.length - 1) {
      setIndex(0);
      audioPlayer.current.src = music[0].src;
      setCurrentTitle(music[0].title);
      setCurrentSinger(music[0].singer);
      setCurrentImg(music[0].img);
      toggleIdLyric(music[0].id);

      if (isPlaying) {
        audioPlayer.current.play();
      }
    } else {
      setIndex(prev => prev + 1);
      audioPlayer.current.src = music[index + 1].src;
      setCurrentTitle(music[index + 1].title);
      setCurrentSinger(music[index + 1].singer);
      setCurrentImg(music[index + 1].img);
      toggleIdLyric(music[index + 1].id);

      if (isPlaying) {
        audioPlayer.current.play();
      }
    }
  }

  const onEndedEventHandler = () => {
    toggleNext();
  }

  const onImageClickedHandler = (url, captionImg) => {
		const myModal = document.getElementById("myModal");
		const modalImg = document.getElementById("modalImg");
		// const caption = document.querySelector(".caption");

		myModal.style.display = "block";
		modalImg.src = url;
		// caption.innerHTML = captionImg;
	}

	const onCloseHandler = () => {
		const myModal = document.getElementById("myModal");
    myModal.style.display = "none";
  }

  return (
    <div className="music-player__container">
      <div className="music-player__box-img">
        <img 
          className='music-player__img animate-fading__player lazyload'
          src={currentImg}
          alt='icon music'
          onClick={() => onImageClickedHandler(currentImg)} />
        <div id="myModal" className="modal">
					<span className="close" onClick={onCloseHandler}>
						<MdArrowBackIosNew />
					</span>
				  	<img className="modal-content" id="modalImg" alt="" />
				  	{/* <div className="caption"></div> */}
				</div>
      </div>
      <Link className="music-player__link-title" to={`/lyric`}>
        <div className="music-player__box-title">
          <p className="music-player__title">{currentTitle}</p>
          <p>{currentSinger}</p>
        </div>
      </Link>
      <div className="music-player__button">
        <div className="music-player__prev">
          <button aria-label='Add to previous song' onClick={togglePrev}><MdArrowBackIosNew /></button>
        </div>
        <div className="music-player__play">
          <button aria-label='Add to play and pause a song' onClick={togglePlay}>{isPlaying ? <MdPause /> : <MdPlayArrow />}</button>
        </div>
        <div className="music-player__next">
          <button aria-label='Add to next song' onClick={toggleNext}><MdArrowForwardIos /></button>
        </div>
      </div>
      <div className="music-player__box-track">
        <audio ref={audioPlayer} src={currentSong} onEnded={onEndedEventHandler} />
        <input aria-label='Range of songs' className="play-track" type="range" max={duration} value={`${elapsed == duration ? 0 : elapsed}`} />
      </div>
    </div>
  );
}

export default MusicPlayer;
